import React, { Component } from 'react';
import { translate } from 'react-i18next';
import Container from './Container';

import o2Logo from '../images/customer_logos/o2.png';
import admiralLogo from '../images/customer_logos/admiral.png';
import welshGovLogo from '../images/customer_logos/welsh_gov.png';
import wruLogo from '../images/customer_logos/wru.png';
import fsbLogo from '../images/customer_logos/fsb.png';
import tyHafanLogo from '../images/customer_logos/ty_hafan.png';

class CustomerLogos extends Component {
  render() {
    const { t } = this.props;

    const logos = [
      { company: 'O2', logo: o2Logo },
      { company: 'Admiral', logo: admiralLogo },
      { company: 'Welsh Government', logo: welshGovLogo },
      { company: 'WRU', logo: wruLogo },
      { company: 'FSB', logo: fsbLogo },
      { company: 'T Hafan', logo: tyHafanLogo },
    ];

    return (
      <Container isWide>
        <div className="CustomerLogos">
          <h5>{t('heading')}</h5>
          <div className="CustomerLogos__grid">
            {logos.map(({ company, logo }) => (
              <div className="CustomerLogos__logo" key={company}>
                <img src={logo} alt={company} />
              </div>
            ))}
          </div>
        </div>
      </Container>
    );
  }
}

export default translate('CustomerLogos')(CustomerLogos);



